<template>
    <div class="party">
     <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
       <img  v-if="images.length == 0 " src="@/assets/images/party/default.png" />
        <van-swipe :autoplay="5000" :height="170" v-else  >
          <van-swipe-item v-for="(image, index) in images" :key="index" >
            <img   v-lazy="image.pic.op_opic" />
          </van-swipe-item>
        </van-swipe>
        <div class="box_tab">
          <div class="tab_icons">
            <router-link to="/build"> 
              <img src="@/assets/images/party/icon_1.png" />
              <div class="icons_txt">党支建设</div>
            </router-link>
          </div>
          <div class="tab_icons">
            <router-link to="/application"> 
              <img src="@/assets/images/party/icon_2.png" />
              <div class="icons_txt">入党申请</div>
            </router-link>
          </div>
          <div  class="tab_icons">
             <router-link to="/transfer"> 
              <img src="@/assets/images/party/icon_3.png" />
              <div class="icons_txt">组织关系转移</div>
             </router-link>
          </div>
          <div class="tab_icons">
             <router-link to="/idenauth"> 
              <img src="@/assets/images/party/icon_4.png" />
              <div class="icons_txt">党员身份认证</div>
             </router-link>
          </div>
        </div>

        <van-tabs v-model="active" class="list_inner">
          <van-tab title="媒体专栏">
            <van-list
              v-model="loading1"
              :finished="finished1"
              @load="onLoad1"
            >
              <div
                v-for="(item,k) in tableData1"
                :key="k"
                 @click="goNews(item.id)"
              >
              <div class="list_other">
                <div class="list_other_left">  
                  <div class="list_title">
                  {{item.title}}
                  </div>
                  <div class="list_time">{{item.publish}}</div>
                </div>
                <div class="list_other_right" >
                    <img  v-lazy="item.pic.op_opic"   class="list_img" />
                </div>
              </div>
              </div>
              <no-data :open="finished1"></no-data>
            </van-list>
          </van-tab>
          <van-tab title="通知公告">
            <van-list v-model="loading2" :finished="finished2" @load="onLoad2">
            <div v-for="(item,k) in tableData2" :key="k"   @click="goNews(item.id)">
                <div class="list_other">
                  <div class="list_other_left">  
                    <div class="list_title">
                    {{item.title}}
                    </div>
                    <div class="list_time">{{item.publish}}</div>
                  </div>
                  <div class="list_other_right" >
                    <img  v-lazy="item.pic.op_opic"   class="list_img" />
                  </div>
                </div>
              </div>
            </van-list>
            <no-data :open="finished2"></no-data>
          </van-tab>
          <van-tab title="党建教育">
             <van-list v-model="loading3" :finished="finished3" @load="onLoad3">
            <div v-for="(item,k) in tableData3" :key="k"   @click="goNews(item.id)">
                <div class="list_other">
                  <div class="list_other_left">  
                    <div class="list_title">
                    {{item.title}}
                    </div>
                    <div class="list_time">{{item.publish}}</div>
                  </div>
                  <div class="list_other_right" >
                    <img  v-lazy="item.pic.op_opic"    class="list_img" />
                  </div>
                </div>
              </div>
            </van-list>
            <no-data :open="finished3"></no-data>
          </van-tab>
          <van-tab title="党建活动">
             <van-list v-model="loading4" :finished="finished4" @load="onLoad4">
             <div v-for="(item,k) in tableData4" :key="k" class="list_act"   @click="goAct(item.actid)">
                  <img v-lazy="item.pic_url.op_opic" class="list_act_img" />
                  <div class="list_title">  {{item.name}} </div>
                  <div class="list_act_txt">
                    <div class="list_act_left font_green">
                      <template v-if="item.order != 3">
                          <div class="act_icons" v-if="item.signUsers.length == 0">
                              <img src="@/assets/images/party/icon_default.png" />
                          </div>
                          <div class="act_icons" v-if="item.signUsers.length > 0">
                            <img v-for="(i,k) in item.signUsers" :key=k   v-lazy="i.img">
                          </div>
                      </template>
                     </div> 
                    <div class="list_act_middle act_place font_green" v-if="item.order == 1"> 
                      <template v-if="item.signUsers.length == 0">
                      进行中
                      </template>
                      <template v-else>
                        已有{{item.signNum}}人报名
                      </template>
                      </div>
                    <div v-if="item.order == 2" class="list_act_right act_place font_green"> 活动名额已满</div>
                  </div>
                  <div class="act_finish" v-if="item.order == 3"><img src="@/assets/images/party/finish.png"></div>
                </div> 
            </van-list>
            <no-data :open="finished4"></no-data>
          </van-tab>
        </van-tabs> 
     </van-pull-refresh>
     </div>
</template>

<script>  
  import * as utils from '@/common/utils.js'
  import NoData from "@/components/NoData.vue";
  export default {
    name: 'pHome',
    data() {
      return {
         active: 0,
         isLoading: false,
         images: [{
           pic: {}
         }], 
        list: [],
        loading1: false,
        finished1: false,
        tableData1: [],
        page1: 1,
        loading2: false,
        finished2: false,
        tableData2: [],
        page2: 1,
        loading3: false,
        finished3: false,
        tableData3: [],
        page3: 1,
        loading4: false,
        finished4: false,
        tableData4: [],
        page4: 1,
        pageSize: 4,
        zone_id: '',   
      }
    },
    components: {NoData},
    created() {
      //获取zone_id  首入口进入都需要存zone_id
      this.zone_id = utils.getQueryString(window.location.href, 'zone_id')||1;
      if(this.zone_id) {
        localStorage.h5_zone_id = this.zone_id;
      }
      this.getBanner();
      // 获取个人信息，存入session
      this.getUser();
   
    },
    methods: {
      getUser() {
          this.axios.post('/app/appuser/getuserinfo',{}).then(res => {
                if (res.s == 1) {  
                    sessionStorage.users= JSON.stringify({
                      name: res.d.username,
                      phone: res.d.phone,
                      company: res.d.enterprise_name,
                    })
                } 
            }) 
      },
       onLoad1() {
      // 异步更新数据 
          this.axios.get('/app/news/partyNews?zone_id='+this.zone_id+'&type='+(this.active+4)+'&page='+this.page1+'&pageSize='+this.pageSize).then(res => {
            this.loading1 = false;
            if (res.s == 1) { 
              this.tableData1 = this.tableData1.concat(res.d.data);
              if(res.d.last_page <= this.page1) {
                  this.finished1 = true
              }else {
                this.page1++;
              }
              
            } else {
              this.finished1 = true;

            }
          })  
        },
        onLoad2() {
     // 异步更新数据 
          this.axios.get('/app/news/partyNews?zone_id='+this.zone_id+'&type='+(this.active+4)+'&page='+this.page2+'&pageSize='+this.pageSize).then(res => {
            this.loading2 = false;
            if (res.s == 1) { 
                 this.tableData2 = this.tableData2.concat(res.d.data);
              if(res.d.last_page <= this.page2) {
                  this.finished2 = true;
              }else {
                this.page2++;
              }
              
            } else {
              this.finished2 = true;

            } 
          })  
        },
        onLoad3() {
     // 异步更新数据 
          this.axios.get('/app/news/partyNews?zone_id='+this.zone_id+'&type='+(this.active+4)+'&page='+this.page3+'&pageSize='+this.pageSize).then(res => {
            this.loading3 = false;
            if (res.s == 1) { 
                  this.tableData3 = this.tableData3.concat(res.d.data);
              if(res.d.last_page <= this.page3) {
                  this.finished3 = true;
              }else {
                this.page3++;
              }
              
            }  else {
              this.finished3 = true;

            }
          })  
        },
        onLoad4() {
     // 异步更新数据 
          this.axios.get('/app/unionact/partyActList?zone_id='+this.zone_id+'&page='+this.page4+'&pageSize='+this.pageSize).then(res => {
            this.loading4 = false;
            if (res.s == 1) { 
              this.tableData4 = this.tableData4.concat(res.d.data);
              if(res.d.last_page <= this.page4) {
                  this.finished4 = true;
              }else {
                this.page4++;
              }
              
            }else {
              this.finished4 = true;

            }
          })  
        },
        getBanner() {
          this.axios.get('/app/party/getBanner?zone_id='+this.zone_id).then(res => {
            if (res.s == 1) { 
              this.images = res.d; 
              
            } 
          }) 
        },
        onRefresh() {
          location.reload();
        },
        goNews(id) {
               this.$router.push({
                path: '/news/'+id,
              })
        },
        goAct(id) {
             if (process.env.NODE_ENV === 'development') { 
                 location.assign('/index.html?id='+id+'&_allowShare=1#/activity')
            } else { 
                location.assign('/user/index.html?id='+id+'&_allowShare=1#/activity')
            }
      
        }

    }
  }
</script>

<style lang="scss" scoped>
@function rem($px) {
    @return $px / 50px * 1rem;
}
  .party {
    .box_tab {
      display: flex;
      background: #fff;
      text-align: center;
      padding-bottom: rem(10px);
      border-bottom: rem(1px) solid #eee;
      .tab_icons {
        flex: 1;
        img {
         padding: rem(10px) 0 rem(4px) 0;
         width: rem(34px);

        }
        .icons_txt {
          font-size: rem(12px);
          color: #666;

        }
      }
    }
    .list_inner {
      background: #fff;
      margin-top: rem(10px);
      border-top: rem(1px) solid #eee; 
       .van-tabs__wrap {
        height: rem(55px);
        line-height: rem(55px);
        border-bottom: rem(1px) solid #eee;
      } 
      .van-tab {
        font-size: rem(15px);
        height: rem(55px);
        line-height: rem(55px);
      }
      .list_other  {
        padding: rem(15px); 
        position: relative;
        border-bottom: rem(1px) solid #eee;
        display: flex;
        .list_time {
          position: absolute;
          bottom: rem(10px);
          font-size: rem(12px);
          color: #999;
        }
        .list_other_left {
          flex: 1;
        }
        .list_other_right {
          width: rem(120px);
          padding-left: rem(10px);
        }
      }
      .list_img {
        height: rem(76px);
        width: rem(120px);
      }

    }
    .list_title { 
      line-height: rem(24px); 
      margin-bottom: rem(13px);
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }
    .list_act {
        padding: rem(15px) rem(15px) rem(15px)  rem(15px); 
        position: relative;
        border-bottom: rem(1px) solid #eee;
        .list_title {
          padding-top: rem(10px);

        }
    }
    .local_icon {
      width: rem(14px);
      position: relative;
      top: rem(-2px);
    }
    .list_act_txt {
      display: flex;
      margin-top: rem(10px);
      font-size: rem(13px);
      line-height: rem(28px);
      
      .list_act_left {
        flex: 1;
      }
      .list_act_middle{
        flex: 1;
      }
      .list_act_right{
        flex: 1;
      }
    }
    .list_act_img { 
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    } 
    .act_place {
      font-size: rem(13px);
      text-align: right;
    }
    .act_finish {
      position: relative;
      width: rem(70px);
      top: rem(-16px);
      height: rem(30px);
      left: rem(30px);
    }
    .act_icons {
      img {
        width: rem(28px);
        height: rem(28px);
        border-radius: rem(28px);
        margin-right: rem(5px);
      }
    }
    .van-hairline--top-bottom::after {
      border-width: 0;  
    }
  }
</style>
